// our blogs info
.bg-gray{
    background: $light-gray;
}

.blog-cards{
    margin-top: 2rem;
    &__des{
        color: $input-label;
    }
    // mobile
    @media (min-width: $md) {
        margin-top: 3.5rem;
    }
    figcaption{
        @media (max-width: $md-max) {
            padding-right: 2rem;
        }
    }
}
.read-article{
    color: $red;
    @extend .fnt-sm;
    @extend .fnt-bold;
    @include inline-flex();
    @include align-items(center);
    margin-top: 1rem;
    @media (min-width: $md) {
        margin-top: 1.5rem;
    }
    i{
        font-size: 20px;
    }
}