// clients testimonials
.bg-theme-gradient{
    background: linear-gradient(227deg, #d11450 40%, #ee2e24 100%);
}
.testimonial-content{
    padding: 2rem 1rem;
    color: $thick-black;
    @media (min-width: $md) {
        padding-left: 4rem;
        padding-right: 2rem;
    }
}
.author-info{
    padding-top: 1.5rem;
    @media (min-width: $md) {
        padding-top: 4rem;
    }
    &__company{
        color: rgba(0, 0, 0, 0.47);
    }
}