// Inputs

form,
.field {
  position: relative;
}

// Input groups

.input-group {
  &-col {
    display: flex;
    flex-direction: column;
  }

  &-row {
    display: flex;
    flex-direction: row;
  }
}


// Input Fields

.input-field {
	position: relative;
	display: block;
  background: $input-bg;
	color: $input-text;
  border: 1px solid $input-border;
  padding: 1rem;
  border-radius: 0.25rem;
  @extend .fnt-sm;
  &.block {
    width: 100%;
  }

  &:focus,
  &.focus {
    outline: none;
    box-shadow: 0 0 8px rgba(239,64,35,.2);
  }

  &::-ms-clear {
    display: none;
  }

  &:-webkit-autofill {
    background-color: transparent !important;
    -webkit-box-shadow: 0 0 0 1000px $white inset !important;
    border: 1px solid $input-border !important;
    // border: none;
  }

  &:-webkit-autofill:focus {
    background-color: transparent !important;
    -webkit-box-shadow: 0 0 0 1000px $white inset !important;
  }
}

.input-field ~ .input-icon {
  position: absolute;
  right: 1rem;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
}

.m-input {
  padding: 1.6rem 1rem 1rem 1rem;
}

.amount-input {
  padding-right: 7rem;
}

.m-input ~ .input-label {
	pointer-events: none;
  position: absolute;
	top: 50%;
  left: 1rem;
  transform: translateY(-50%);
  // transition: all 0.25s cubic-bezier(0.2, 0, 0.03, 1);
  transition: all 0.25s ease;
  backface-visibility: hidden;
  font-size: $input-label-default-size;

  &.active {
    top: 28%;
    font-size: $input-label-active-size;
  }
}

.input-label {
  user-select: none;
  color: $input-label;
  font-size: $input-label-default-size;
}


// Button Inside input
.input-btn-inside {
  position: absolute;
  right: 1rem;
  top: 50%;
  transform: translateY(-50%);
}


/* form-info*/


.form-search{
  position: relative;

  input{
    padding-right: 30px !important;
  }

  .icon{
    position: absolute;
    right: 14px;
    top: 50%;
    -webkit-transform: translate(0, -43%);
        -ms-transform: translate(0, -43%);
            transform: translate(0, -43%);
    font-size: 1rem;
  }
}

// Auto suggest search input

.autosuggest {
  position: absolute;
  z-index: 99;
  /*position the autocomplete items to be the same width as the container:*/
  top: 100%;
  left: 0;
  right: 0;
  display: none;
  box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  max-height: 300px;
  overflow-y: auto;

  &.show {
    display: block;
  }

  &__item {
    padding: 0.8rem 1rem;
    cursor: pointer;
    background-color: $white;
    transition: all 0.5s;
    @extend .fnt-sm;
    color: $text-primary;

    &:hover {
      background-color: $hover-secondary;
    }

    &.active {
      background-color: $active-secondary;
    }

    .unfocused {
      margin-left: 4px;
    }
  }
}

// Placeholder

input::-webkit-input-placeholder {
  color: $input-label !important;
  font-family: $b-regular !important;
  font-size: 14px !important;
}

input:-ms-input-placeholder {
  color: $input-label !important;
  font-family: $b-regular !important;
  font-size: 14px !important;
}

input::-moz-placeholder {
  color: $input-label !important;
  font-family: $b-regular !important;
  font-size: 14px !important;
}

input:focus::-webkit-input-placeholder {
  color: $input-label !important;
  font-family: $b-regular !important;
  font-size: 14px !important;
}

input:focus:-ms-input-placeholder {
  color: $input-label !important;
  font-family: $b-regular !important;
  font-size: 14px !important;
}

input:focus::-moz-placeholder {
  color: $input-label !important;
  font-family: $b-regular !important;
  font-size: 14px !important;
}


