/* Form Error Styling */

.field.error,
.input-group.error {

  .input-field {
    border: 1px solid $red;
    transition: all 0.5s;
  }
}

.ui.error.message {
  color: $red;
  font-size: 13px;
  padding-top: 6px;
}

