
// header info
.navbar-expand-md {
	transition: all .6s;
	padding: 1rem 1rem;
	@media (min-width: $md) {
		padding: 1.5rem 1rem;
	}
	.nav-link{
		color: rgba(255, 255, 255, .87);
		@extend .fnt-sm;
		.icon{
			font-size: 22px;
		}
	}
	.navbar-brand{
		img{
			@media (max-width: $md-max) {
				height: 20px;
				width: 70px;
			}
		}
	}	
}

// onscroll changing the headervalues
.navbar-expand-md.on{
	transition: all .6s;
	background: $white;
	box-shadow: 0 0 3px 0 rgba(0,0,0,.1);
	padding: .8rem 1rem;
	@media (min-width: $md) {
		padding: 1.2rem 1rem;
	}
	.nav-link{
		color: $thick-black;
	}
	.btn--white{
		border: 1px solid $red;
	}
	.navbar-brand{
		span{
			color: $thick-black !important;
		}
	}
}