// footer info

.footer-link{
    color: $footer-nav-text;
    @extend .fnt-sm;
}

.footer{
    &__text {
        line-height: 15px;
    }
    // main-links
    &__main-links{
        @media (min-width: $md) {
            padding: 0 1rem;
        }
        a{
            display: inline-block;
            margin-bottom: .5rem;
            @media (max-width: $md-max) {
                margin-right: 1rem;
            }
            @media (min-width: $md) {
                display: block;
            }
            &:last-child{
                margin-bottom: 0;
            }
        }
    }
    //security questions
    &__security{
        border-top: $border-footer;
        border-bottom: $border-footer;
        @extend .theme-text-primary;
        @extend .fnt-sm;
        padding: 1rem 0;
    }
    //footer social links
     &__social-link{
        height: 30px;
        width: 30px;
        border-radius: 50%;
        border: 1px solid $gray;
        @include inline-flex();
        @include align-items(center);
        @include justify-content(center);
        margin-right: .5rem;
        .icon{
            @extend .fnt-sm;
            @include flex();
            color: $gray;
        }
    }
    // footer social
    &__sub-social{
        border-top:  $border-footer;
        padding: 1.2rem 0;
        &__mobile{
            @media (max-width: $md-max) {
                @include flex-direction(column);
            }
        }
    }
    &__tag-line{
        color: rgba(0,0,0,.54);
    }
}

// sub-footer info

.sub-footer{
    border-bottom: $border-footer;
    &__list{
        li{
            border-right: $border-footer;
            &:last-child{
                border-right: none;
            }
            // mobile
            @media (max-width: $md-max) {
                margin-bottom: 1rem;
                &:nth-last-child(-n+2){
                    margin-bottom: 0;
                }
            }
        }
        a{
            color: $footer-nav-text;
        }
    }
}

// apps info text

.footer-border-links{
    @media (min-width: $md) {
        border-right: $border-footer;
    }
}

.app-view{
    background: $apps-bg;
    border-radius: 40px;
    padding: .4rem 1rem;
    font-family: 'Brown-Bold';
    @include align-items(center);
    @media (min-width: $md) {
        padding: .6rem 1.2rem;
    }
}

.apps-download{
    &__text{
        @include fonts(8px, 10px, 10px, null, null);
        line-height: 12px !important;
    }
}

// footer destination links

.footer-dest-nav{
    border-top: $border-footer;
    padding: 2rem 1rem;
    @media (min-width: $md) {
        padding: 2rem 5rem;
    }
    &__link{
        @extend .fnt-xs;
        color: $gray;
        &:after{
            content: '/';
            padding: 0 .4rem;
            display: inline-block;
        }
    }
}

