// amount
.amount-info{
    border: 1px solid rgba(0, 0, 0, 0.15);;
    border-radius: 3px;
    padding: .6rem;
    color: $gray;
}

input[type="checkbox"] {
    display: none;  
}
.checkbox-text {
    position: relative;
    transition: all ease .3s;
    border-radius: 2px;
    padding: 0.6rem 1.7rem;
    border: 1px solid $dropdown-border;
    z-index: 1;
    cursor: pointer;
    @include flex();
    @include justify-content(center);
    @include flex-direction(column);
    max-width: 100%;
    width: 160px;
    @media (min-width: $lg) {
        padding: 0.5rem 1rem;
    }
    @media (min-width: $xl) {
        padding: 1rem 1.5rem;
    }
    &:before {
        content: "";
        position: absolute;
        left: 0;
        bottom: 0;
        height: 100%;
        width: 0;
        z-index: -1;
        transition: all ease .3s;
        border-radius: 2px;
    }
}
.selection-fileds {
    @include flex();
    @extend .fnt-sm;
    margin-bottom: 2.5rem;
    @media (max-width: $md-max){
        @include flex-direction(column);
    }
    label{
        @include flex();
    }
    i{
        @include fonts(20px, 26px, 36px, null, null);
        @include justify-content(center);
    }
    & input[type="checkbox"]:checked + .checkbox-text  {
        // color: #fff;
    }
    & input[type="checkbox"]:checked + .checkbox-text:before {
        // background-color: $pink;
        width: 100%;
        border: 1px solid $green;
    }
    & input[type="checkbox"]:checked + .checkbox-text:after {
        position: absolute;
        top: 0;
        right: 0;
        height: 25px;
        width: 25px;
        background: $green;
        border-radius: 100%;
        transform: translate(45%, -50%);
        font-family: 'oyo';
        content: "\78";
        padding-top: 1px;
        padding-left: 2px;
        color: $white;
    }
}

// payment-options popup info

.card-expiry-info{
    @include flex();
    @include align-items(center);
    @include justify-content(space-between);
    padding-top: .05rem;
    padding-bottom: .05rem;
    .ui.selection.dropdown{
        min-width: 110px;
        border: 0;
        &:focus{
            box-shadow: none !important;
        }
    }
    .ui.selection.dropdown .menu{
        max-height: 250px;
    }
}