

//Brown fonts

@font-face {
    font-family: 'Brown-Regular';
    src: url('../fonts/Brown-Regular/Brown-Regular.eot?#iefix') format('embedded-opentype');
    src: url('../fonts/Brown-Regular/Brown-Regular.woff2') format('woff2'), url('../fonts/Brown-Regular/Brown-Regular.woff') format('woff'), url('../fonts/Brown-Regular/Brown-Regular.ttf') format('truetype'), url('../fonts/Brown-Regular/Brown-Regular.svg#Brown-Regular') format('svg');
    font-weight: normal;
    font-style: normal;
  }
  
  /** Brown-regular **/
  
  @font-face {
    font-family: 'Brown-Bold';
    src: url('../fonts/Brown-Bold/Brown-Bold.eot?#iefix') format('embedded-opentype');
    src: url('../fonts/Brown-Bold/Brown-Bold.woff2') format('woff2'), url('../fonts/Brown-Bold/Brown-Bold.woff') format('woff'), url('../fonts/Brown-Bold/Brown-Bold.ttf') format('truetype'), url('../fonts/Brown-Bold/Brown-Bold.svg#Brown-Bold') format('svg');
    font-weight: normal;
    font-style: normal;
  }

// custom fonts
@font-face {
  font-family: "oyo-business";
  src:url("../fonts/oyo-business/oyo-business.eot");
  src:url("../fonts/oyo-business/oyo-business.eot?#iefix") format("embedded-opentype"),
    url("../fonts/oyo-business/oyo-business.woff") format("woff"),
    url("../fonts/oyo-business/oyo-business.ttf") format("truetype"),
    url("../fonts/oyo-business/oyo-business.svg#oyo-business") format("svg");
  font-weight: normal;
  font-style: normal;
}