/**   Common css for all the pages   **/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
sub,
sup,
tt,
var,
b,
u,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  /* font-size: 100%;*/
  font: inherit;
  vertical-align: baseline;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

ol,
ul {
  list-style: none;
}

a {
  text-decoration: none;
  &:hover {
    color: #0056b3;
    text-decoration: none;
    transition: all 0.5s ease;
  }
}

/*=============================================
		body Fonts
=============================================*/

body {
  font-family: 'Brown-Regular';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  p{
    @extend .fnt-sm;
  }
}

.h1 {
  @include fonts(30px, 36px, 40px, null, 'Brown-Bold');
}

.h2{
  @include fonts(20px, 22px, 24px, null, 'Brown-Bold');
}

.h3{
  @include fonts(18px, 20px, 20px, uppercase, 'Brown-Bold');
  color: $thick-black;
}

.fnt-xl {
  @include fonts(18px, 20px, 20px, null, null);
}

.fnt-lg {
  @include fonts(14px, 16px, 18px, null, null);
}

.fnt-md {
  @include fonts(14px, 16px, 16px, null, null);
}

.fnt-sm {
  @include fonts(12px, 14px, 14px, null, null);
}

.fnt-xs {
  @include fonts(11px, 12px, 12px, null, null);
}

// colors

.theme-text-primary{
  color: $gray;
}
.theme-text-secondary{
  color: $red;
}
.fnt-bold{
  font-family: 'Brown-Bold';
}

// borders

.border{
  &-top{
    border-top: $border-footer;
  }
  &-bottom{
    border-bottom: $border-footer;
  }
  &-right{
    border-right: $border-footer;
  }
  &-left{
    border-left: $border-footer;
  }
}

.img-fit{
  object-fit: cover;
}

// override bootstrap

.cnt-max1600{
  max-width: 1600px;
}

.flex-clm-mb{
  @media (max-width: $md-max) {
    @include flex-direction(column-reverse);
  }
}
