// buttons info
.btn {
    padding: .65rem 2rem;
    @extend .fnt-sm;
    border-radius: 26px;
    cursor: pointer;
    &--primary{
        background: $red;
        color: $white;
    }
    &--white{
        background: $white;
        color: $red;
    }
    &--sucess{
        background: $green;
        color: $white;
        @extend .fnt-bold;
    }
    &--sm{
        padding: .55rem 2.2rem;
    }
    &--lg{
        padding: .68rem 3rem;
        @media (min-width: $md) {
            padding: 1rem 4rem;
        }
    }
}

// close button
.close-btn{
    background: transparent;
    border: none;
    cursor: pointer;
    position: relative;
    top: 6px;
    color: $gray;
    &:focus{
        box-shadow: none;
        outline: none;
    }
}
