
// Basic on focus slider elements removing border
.slick-arrow{
    &:focus{
        outline: none;
        box-shadow: none;
    }
}
// prev and next arrow circle components

.slick-arrow{
    // text-indent: -9999px;
    position: absolute;
    background: 0 0;
    width: 45px;
    border: 1px solid $slick-arrows;
    color: $red;
    height: 45px;
    // -webkit-border-radius: 50%;
    background: $white;
    border-radius: 50%;
    cursor: pointer;
    bottom: 0%;
    right: 0;
    @media (min-width: $md) {
        bottom: -20%;
    }
    &--left{
        left: 2%;
        @media (min-width: $md) {
            left: 0;
        }
    }
    &--right{
          right: 2%;
        @media (min-width: $md) {
            right: 0;
        }
    }
}
.icon-arrow{
    @include flex();
    @include justify-content(center);
    &--left{
        transform: rotate(-180deg);
    }
}

// clients brands info

.client-brands{
    padding: 4rem 2rem 3.5rem 2rem;
    @media (min-width: $md) {
        padding: 4.5rem 2rem 5rem 2rem;
    }
}

// brands overriding the slick-slider css
.client-brands{
    .slick-track{
        display: flex;
        @include align-items(center);
        img{
            margin: 0 auto;
        }
    }
    .slick-slide{
        &:focus{
            outline: none;
            box-shadow: none;
        }
    }
    // prev and next buttons
    .slick-prev, .slick-next{
        position: absolute;
        top: 45%;
        background: transparent;
        border: none;
        color: $gray;
        cursor: pointer;
    }
    .slick-prev{
        left: 0;
    }
    .slick-next{
        right: 0;
    }
}

// slick disable arrows

.slick-disabled{
    cursor: default !important;
    opacity: 0.8;
    color: $slick-arrows !important;
}

// slick testimonials css info

.testimonial-slider{
    .slick-dots{
        display: flex !important;
        @include align-items(center);
        @include justify-content(center);
        margin-top: 2rem;
        li{
            margin: 0 .4rem;
        }
    }
    .slick-dots li button{
        text-indent: -9999px;
        border-radius: 50%;
        border: 0;
        height: 10px;
        width: 10px;
        transition: all ease .4s;
        cursor: pointer;
        padding: 0;
        background: rgba(255, 255, 255, .28);
        @media (min-width: $md) {
            height: 12px;
            width: 12px;
        }
        &:focus{
            outline: none;
            box-shadow: none;
        }
    }
    .slick-dots li.slick-active button{
        background: $white;
    }
}

//oyo peoperities slider

.oyo-area-slider{
    // transform: translateX(-12%);
    // margin-left: -10%;
    position: relative;
    @media (max-width: $md-max) {
        padding: 4rem 0;
    }
    @media (min-width: $md) {
        margin-left: -10%;
    }
    .slick-slide{
        padding-right: 1rem;
        &:focus{
            outline: none;
        }
    }
    .slick-prev{
        right: 20%;
        @media (min-width: $md) {
            right: 14%;
        }
        @media (min-width: 1600px) {
            right: 10%;  
        }
    }
    .slick-next{
        right: 4%;
    }
    // Onhover image info
    &__view{
        position: relative;
        overflow: hidden;
        &:hover{
            .overlay{
                opacity: 1;
            }
        }
    }
    &__content{
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        height: 100%;
        width: 100%;
        @include flex();
        @include align-items(flex-end);
        // transition: all .3s cubic-bezier(.25,.1,.25,1);
        padding: 1.2rem 1.4rem 0 1.4rem;
        &:hover{
            @include align-items(flex-start);
            @include flex-direction(column);
            transition: all .3s cubic-bezier(.25,.1,.25,1);
            padding: 4rem 1.4rem;
            .content-view{
               opacity: 1;
               display: block;
            }
        }
    }
}

// our blogs slider info

.js-blog-slider{
    .slick-arrow{
        top: 20%;
        z-index: 10;
        &--left{
            transform: translateX(-10%);
        }
        &--right{
            transform: translateX(10%);
        }
    }
    .slick-disabled{
        opacity: 1;
    }
}

// Responsive slider for our benifits
 .responsive {
     // 
     .slick-arrow{
         @media (max-width: $md-max) {
             bottom: -12%;
         }
        //  .slick-next{ 
        //      left: 10%;
        //  }
     }
    @media (min-width: $md) {
        .slick-next{ 
            position: absolute;
            left: 0;
            bottom: 0;
            transform: rotate(-90deg);
        }
        .slick-prev {
            position: absolute;
            left: 0;
            bottom: -10%;
            transform: rotate(270deg);
        }
    }
}

