// business cards
.business-cards{
    display: block;
    border: 2px solid rgba(74, 74, 74, 0.13);
    padding: 2rem 1.5rem;
    width: 100%;
    @media (min-width: $md) {
        padding: 3rem 4rem;
    }
    figcaption{
        margin-top: 1.5rem;
        @media (min-width: $md) {
            margin-top: 2rem;
        }
    }
    &__title{
        color: $thick-black;
        margin-bottom: .8rem;
    }
    &__des{
        color: $input-label;
    }
    &:hover, &:focus{
        box-shadow: 0 2px 14px rgba(132, 135, 138, 0.53);
        border-color: $red;
        transition: all .5s ease;
    }
}