// banner info
.banner-info{
    background: url('../img/home/Banner-image.png') no-repeat;
    // height: 650px;
    // background-size: 100% 100%;
    padding-top: 5.5rem;
    position: relative;
    @media (min-width: $md) {
        padding-top: 8.5rem;
        background-size: 100%;
    }
    &:after{
        content: '';
        position: absolute;
        height: 22rem;
        background: $white;
        width: 100%;
        left: 0;
        right: 0;
        bottom: 0;
        @media (min-width: $md) {
            height: 10rem;
        }
    }
}

.business-solution{
    @media (max-width: $md-max) {
        margin-bottom: 1.5rem;
    }
    @media (min-width: $md) {
        padding: 0 3rem;
    }
}

// Rewards info

.bg-sudeo{
    position: relative;
    &::after{
        content: '';
        position: absolute;
        height: 10rem;
        width: 100%;
        left: 0;
        right: 0;
        bottom: 0;
        background: $light-gray;
    }
}

.media--responsiv{
    @media (max-width: $md-max) {
        @include flex-direction(column);
        text-align: center;
    }
}

.section-pd{
    padding: 3.5rem 0;
    @media (min-width: $md) {
        padding: 6rem 0;
    }
}
.media-heading{
    letter-spacing: 2px;
    color: $thick-black;
}
.block-list{
    &__item{
        display: inline-block;
        position: relative;
        color: $gray34;
        @extend .fnt-sm;
        margin-right: 1.5rem;
        &::before{
            content: '';
            position: absolute;
            left: -.8rem;
            top: .4rem;
            height: 6px;
            width: 6px;
            border-radius: 50%;
            background: $gray34;
        }
    }
}

// Rewards info

.scroll-content{
    @media (min-width: $xl) {
        padding-right: 8rem;
    }
}

// cutting edge technology

.block-dotted-list{
    &__item {
        padding-bottom: 2rem;
        padding-left: 2rem;
        border-left: 1px dashed rgba(0, 0, 0, 0.2);
        margin-left: .2rem;
        position: relative;
        @media (max-width: $md-max) {
            &:last-child{
                padding-bottom: 0rem;
            }
        }
        &:last-child{
            border-left: none;
        }
        &::before{
            content: '';
            position: absolute;
            height: 10px;
            width: 10px;
            border: 1px solid $red;
            top: 0;
            left: 0;
            border-radius: 50%;
            transform: translateX(-50%);
            background: $light-gray;
        }
        h5{
            @extend .fnt-md;
            color: $thick-black;
            position: relative;
            top: -4px;
        }
        p{
            @extend .fnt-sm;
            color: $gray;
        }
    }
}

// corporate peoperities

.heading-container{
    margin-bottom: 2.5rem;
    @media (min-width: $md) {
        margin-bottom: 4rem;
    }
    .h2{
        letter-spacing: 1.44px;
    }
}

.our-numbers{
    @include fonts(24px, 32px, 34px, null, null);
    display: block;
    line-height: initial;
    position: relative;
    top: -8px;
    line-height: 34px !important;
    color: rgba(0, 0, 0, 0.87);
}

.our-peoperities{
    padding-bottom: 2.5rem;
    @media (min-width: $md) {
        padding-bottom: 3.5rem;
    }
    p{
        color: rgba(0, 0, 0, 0.34); 
    }
}

.content-view{
    opacity: 0;
    transition: all .3s cubic-bezier(.25,.1,.25,1);
    display: none;
    color: $white;
  }
  
  //On-hover overlay

  .overlay{
      background: linear-gradient(197deg, rgba(209, 20, 80, 0.8) 0%, #ee2e24 100%);
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      transition: all .3s cubic-bezier(.25,.1,.25,1);
  }