// white panel info
.white-panel{
    background: $white;
    box-shadow: $panel-shadow;
    border-radius: 5px;
    position: relative;
    z-index: 4;
    &__header{
        // border-bottom: $border-footer;
        color: $black;
        @include flex();
        @include align-items(center);
        @include justify-content(center);
        @media (max-width: $md-max) {
            @include flex-direction(column);
        }
        span{
            @media (max-width: $md-max) {
                border-right: none;
            }
        }
    }
    &__des{
        color: $panel-text-info;
    }
    // Rewards info
    &__register{
        padding: 2rem 1rem;
        @media (min-width: $md) {
            padding: 3rem 10rem;
        }
        p{
            margin: 0 1rem;
            color: $gray;
            @media (min-width: $md) {
                margin: 0 4rem;
            }
        }
    }
}

// Mobile responsive
.img-respns{
    max-width: 100%;
    border-top-left-radius: 4px;
}